import { useState, useEffect } from 'react';
import { BellRinging, User, Users, Clock, CheckCircle, WarningCircle } from '@phosphor-icons/react';
import { storageService } from '../../../services/storage';

const PushHistoryTab = ({ onSelectMember, setActiveTab, pendingApprovals = [], onApprove, onReject }) => {
    const [history, setHistory] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const unsub = storageService.subscribeToPushHistory((data) => {
            setHistory(data);
            setIsLoading(false);
        });
        return () => unsub();
    }, []);

    if (isLoading) {
        return <div style={{ textAlign: 'center', padding: '100px 0', opacity: 0.5 }}> ...</div>;
    }

    return (
        <div className="dashboard-card shadow-lg" style={{ background: 'rgba(25,25,25,0.7)', border: '1px solid rgba(255,255,255,0.05)' }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '30px', paddingBottom: '20px', borderBottom: '1px solid rgba(255,255,255,0.05)' }}>
                <div>
                    <h3 className="outfit-font" style={{ fontSize: '1.4rem', fontWeight: 700, margin: 0 }}>  </h3>
                    <p style={{ margin: '5px 0 0 0', opacity: 0.5, fontSize: '0.85rem' }}>      .</p>
                </div>
                <div style={{ textAlign: 'right' }}>
                    <div style={{ fontSize: '1.1rem', fontWeight: 'bold', color: 'var(--primary-gold)' }}>{history.length}</div>
                    <div style={{ fontSize: '0.7rem', opacity: 0.5 }}>  </div>
                </div>
            </div>

            <div className="card-list">
                {/* [NEW] Pending Approvals Section */}
                {pendingApprovals.length > 0 && (
                    <div style={{ marginBottom: '30px' }}>
                        <h4 style={{ color: '#FFC107', margin: '0 0 15px 0', display: 'flex', alignItems: 'center', gap: '8px' }}>
                            <WarningCircle weight="fill" />    ({pendingApprovals.length})
                        </h4>
                        {pendingApprovals.map(item => (
                            <div key={item.id} className="glass-panel" style={{
                                padding: '20px',
                                marginBottom: '15px',
                                border: '1px solid rgba(255, 193, 7, 0.3)',
                                background: 'rgba(255, 193, 7, 0.05)',
                                borderRadius: '12px'
                            }}>
                                <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '10px' }}>
                                    <div>
                                        <span style={{ 
                                            background: '#FFC107', color: '#000', 
                                            padding: '2px 8px', borderRadius: '4px', fontSize: '0.75rem', fontWeight: 'bold', marginRight: '8px' 
                                        }}>
                                            {item.type === 'low_credits' ? ' ' : (item.type === 'notice' ? ' ' : ' ')}
                                        </span>
                                        <span style={{ fontSize: '0.9rem', color: '#FFF' }}>{item.title}</span>
                                    </div>
                                    <div style={{ fontSize: '0.8rem', opacity: 0.7 }}>
                                        {new Date(item.createdAt?.toDate ? item.createdAt.toDate() : item.createdAt).toLocaleString()}
                                    </div>
                                </div>
                                <div style={{ 
                                    background: 'rgba(0,0,0,0.3)', padding: '15px', borderRadius: '8px', 
                                    marginBottom: '15px', whiteSpace: 'pre-wrap', fontSize: '0.9rem', lineHeight: 1.5 
                                }}>
                                    {item.body}
                                </div>
                                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                    <div style={{ fontSize: '0.85rem', opacity: 0.7 }}>
                                         : {item.targetMemberIds?.length || 0}
                                    </div>
                                    <div style={{ display: 'flex', gap: '10px' }}>
                                        <button 
                                            onClick={() => onReject(item.id)}
                                            style={{
                                                padding: '8px 16px', borderRadius: '8px', border: '1px solid rgba(255,255,255,0.2)',
                                                background: 'transparent', color: '#FFF', cursor: 'pointer'
                                            }}
                                        >
                                            
                                        </button>
                                        <button 
                                            onClick={() => onApprove(item.id, item.title)}
                                            style={{
                                                padding: '8px 20px', borderRadius: '8px', border: 'none',
                                                background: '#FFC107', color: '#000', fontWeight: 'bold', cursor: 'pointer'
                                            }}
                                        >
                                              
                                        </button>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                )}

                <h4 style={{ margin: '0 0 15px 0', opacity: 0.7 }}> </h4>
                {history.length === 0 ? (
                    <div style={{ textAlign: 'center', padding: '60px 0', opacity: 0.5 }}>
                        <BellRinging size={48} style={{ marginBottom: '15px' }} />
                        <p>  .</p>
                    </div>
                ) : (
                    history.map(item => {
                        const isNotice = item.type === 'campaign' || item.type === 'notice';
                        const isClickable = (item.type === 'individual' && item.targetMemberId) || isNotice;
                        
                        let label = '  ';
                        if (item.type === 'campaign') label = ' ';
                        else if (item.type === 'notice') label = ' ';
                        else if (item.type === 'individual') label = `  (${item.memberName || item.targetMemberName || '  '})`;
                        
                        return (
                        <div 
                            key={item.id} 
                            className="glass-panel" 
                            onClick={() => {
                                if (item.type === 'individual' && item.targetMemberId && onSelectMember) {
                                    onSelectMember(item.targetMemberId);
                                } else if (isNotice && setActiveTab) {
                                    setActiveTab('notices');
                                }
                            }}
                            style={{
                            marginBottom: '15px',
                            padding: '20px',
                            border: '1px solid rgba(255,255,255,0.05)',
                            background: 'rgba(255,255,255,0.02)',
                            borderRadius: '12px',
                            position: 'relative',
                            transition: 'all 0.2s ease',
                            cursor: isClickable ? 'pointer' : 'default'
                        }}
                        onMouseEnter={(e) => {
                            if (isClickable) {
                                e.currentTarget.style.background = 'rgba(255,255,255,0.05)';
                            }
                        }}
                        onMouseLeave={(e) => {
                            if (isClickable) {
                                e.currentTarget.style.background = 'rgba(255,255,255,0.02)';
                            }
                        }}
                        >
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', marginBottom: '12px' }}>
                                <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                                    <div style={{
                                        width: '36px',
                                        height: '36px',
                                        borderRadius: '10px',
                                        background: isNotice ? 'rgba(212, 175, 55, 0.15)' : 'rgba(59, 130, 246, 0.15)',
                                        color: isNotice ? 'var(--primary-gold)' : '#3B82F6',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center'
                                    }}>
                                        {isNotice ? <Users size={20} weight="fill" /> : <User size={20} weight="fill" />}
                                    </div>
                                    <div>
                                        <div style={{ fontWeight: 700, fontSize: '1rem', color: isNotice ? 'var(--primary-gold)' : '#3B82F6' }}>
                                            {label}
                                        </div>
                                        <div style={{ fontSize: '0.75rem', opacity: 0.5, display: 'flex', alignItems: 'center', gap: '4px' }}>
                                            <Clock size={12} /> {new Date(item.displayDate).toLocaleString()}
                                        </div>
                                    </div>
                                </div>
                                <div style={{ display: 'flex', alignItems: 'center', gap: '6px' }}>
                                    {isNotice && (
                                        <span style={{
                                            fontSize: '0.65rem',
                                            padding: '2px 8px',
                                            borderRadius: '20px',
                                            background: 'rgba(255,255,255,0.05)',
                                            border: '1px solid rgba(255,255,255,0.1)'
                                        }}>
                                            {item.totalTargets > 0 ? `${item.totalTargets}` : (item.target === 'all' ? '' : '')}
                                        </span>
                                    )}
                                    <span style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                        gap: '4px',
                                        fontSize: '0.65rem',
                                        padding: '2px 8px',
                                        borderRadius: '20px',
                                        background: item.status === 'pending' ? 'rgba(255, 193, 7, 0.1)' : 'rgba(40, 167, 69, 0.1)',
                                        color: item.status === 'pending' ? '#FFC107' : '#28A745',
                                        border: `1px solid ${item.status === 'pending' ? 'rgba(255, 193, 7, 0.2)' : 'rgba(40, 167, 69, 0.2)'}`
                                    }}>
                                        {item.status === 'pending' ? <WarningCircle size={10} /> : <CheckCircle size={10} />}
                                        {item.status === 'pending' ? ' ' : ' '}
                                    </span>
                                </div>
                            </div>
                            <div style={{
                                fontSize: '0.9rem',
                                opacity: 0.9,
                                lineHeight: 1.6,
                                background: 'rgba(0,0,0,0.2)',
                                padding: '12px 15px',
                                borderRadius: '8px',
                                color: 'rgba(255,255,255,0.85)',
                                whiteSpace: 'pre-wrap'
                            }}>
                                {item.body || item.content}
                            </div>
                        </div>
                    );
                    })
                )}
            </div>
        </div>
    );
};

export default PushHistoryTab;
