import { useState } from 'react';
import { ChatCircleText } from '@phosphor-icons/react';
import { storageService } from '../../../services/storage';

const MessageModal = ({ isOpen, onClose, member }) => {
    const [messageText, setMessageText] = useState('');

    if (!isOpen || !member) return null;

    const handleSendMessage = async () => {
        if (!messageText) return;
        await storageService.addMessage(member.id, messageText);
        alert(`${member.name}  .`);
        setMessageText('');
        onClose();
    };

    return (
        <div className="modal-overlay" onClick={onClose}>
            <div className="modal-content" onClick={e => e.stopPropagation()}>
                <h2 className="modal-title">{member.name}  </h2>
                <p style={{ marginBottom: '15px', fontSize: '0.9rem', opacity: 0.7 }}>
                           .
                </p>
                <textarea
                    className="form-input"
                    style={{ height: '150px', resize: 'none', marginBottom: '20px' }}
                    placeholder="  ..."
                    value={messageText}
                    onChange={(e) => setMessageText(e.target.value)}
                />
                <div className="modal-actions">
                    <button onClick={onClose} style={{ padding: '10px 20px', color: 'var(--text-secondary)' }}></button>
                    <button onClick={handleSendMessage} className="action-btn primary">
                        <ChatCircleText size={18} style={{ marginRight: '6px' }} />  
                    </button>
                </div>
            </div>
        </div>
    );
};

export default MessageModal;
